.container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;

    @media screen and (min-width: 480px) {
        width: 480px;
        margin: 0 auto;
    }

    @media screen and (min-width: 768px) {
        padding-left: 40px;
        padding-right: 40px;
        width: 768px;
    }

    @media screen and (min-width: 1366px) {
        padding-left: 15px;
        padding-right: 15px;
        width: 1344px;
    }
}